    import React from 'react';
    import { NavLink } from 'react-router-dom'


const PolymagSection = () => {
    return(
    <section className="about_us section_padding">
        <div className="container">
            <div className="row align-items-center justify-content-between">
                <div className="col-md-6 col-lg-5">
                    <div className="about_us_text">
                        <h2>Decouvrez PolyMag</h2>
                        <p>PolyMag est un magazine ouvert sur le monde de la science et de la culture, témoignant de la diversité des centres d’intérêt des étudiants et des enseignants de l’École Nationale Polytechnique</p>
                        <NavLink to="/polymag" className="btn_1">PolyMag c'est quoi?</NavLink>
                    </div>
                </div>
                <div className="col-md-6 col-lg-6">
                    <div className="learning_img">
                        <img src={process.env.PUBLIC_URL.concat("theme/img/polytechmag.png")} alt="polytechmag"/>
                    </div>
                </div>
            </div>
        </div>
        <br/>

    </section>
    );
}

export default PolymagSection;
