import React from '../../node_modules/react';

import TitleSection from '../Sections/TitleSection';


const About = () => {
    return(
        <div className="">
        <TitleSection title="A Propos"/>

<section class="sample-text-area">
    <div class="container box_1170">
        <p>Le CAP (Club d'Activités Polyvalentes) est un club estudiantin de l'Ecole Nationale Polytechnique, proposant des activités scientifiques, culturelles, caritatives et sportives. Créé en 2010 à l’initiative d’un groupe d’étudiants, qui avaient pour ambition de nettoyer l'école, le club n’a pas cessé de s’agrandir, de se faire connaître et de diversifier ses activités.</p>  
        <p>On y compte aujourd'hui un effectif d’environ 300 membres, qui organisent et prennent part aux nombreux projets captivants. Chaque membre appartient à un des quatre départements au sein du CAP : communication, informations et technologie, ressources humaines et relations extérieurs. Avec cette organisation le club détient à son actif une dizaine d’événements dans divers domaines chaque année.</p> 
    </div>
</section>

<center><img src={process.env.PUBLIC_URL + 'theme/img/about/cap-1-2.jpg'} width="900" length="700" alt="cap"/></center>
<br/>

<div class="container">
    <article class="docs-article" id="section-1">
        <h4>Le club est pourvu de 3 sections pour assurer le déroulement de ses activités :</h4>
        <div class="alert alert-primary" role="alert">
        Une section caritative œuvrant dans la bienfaisance et qui a pour but d’apporter de l’aide aux plus démunis.<br/>
        Une section scientifique qui contribue dans la formation de ses membres, l’enrichit, et leur permet de partager leur passion des sciences.<br/> 
        Une section culturelle qui permet de se détendre de la pression continue des études, et qui elle-même est composée de plusieurs sous-section : musicale, sportive, théâtrale et littéraire.<br/>   
        </div>
    </article>
</div>
</div>
    );
}

export default About;
