import React from 'react';
import { NavLink } from 'react-router-dom'




const NavbarSection = () => {
    return(

    <header className="main_menu home_menu">
        <div className="container">
            <div className="row align-items-center">
                <div className="col-lg-12">

        <nav className="navbar navbar-expand-lg navbar-light">
            <br/>
            <div className="navbar-brand" >
                <img src="https://s.w.org/style/images/about/WordPress-logotype-wmark-white.png" width="130" length="50" alt="logo"/>
             </div>
            <br/>
            
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent"
            aria-expanded="false" aria-label="Toggle navigation">
            <span className="menu_icon"><i className="fas fa-bars"></i></span>
            </button>

            <div className="collapse navbar-collapse main-menu-item" id="navbarSupportedContent">
                <ul className="navbar-nav">
                    <li className="nav-item"><NavLink to="/" className="nav-link">Accueil</NavLink></li>
                    <li className="nav-item"><NavLink to="/actualite" className="nav-link">Actualité</NavLink></li>
                    <li className="nav-item"><NavLink to="/polymag" className="nav-link">PolyMag</NavLink></li>
                    <li className="nav-item dropdown">
                        <NavLink to="/evenements" className="nav-link dropdown-toggle" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Evenements</NavLink>
                            <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                                <NavLink to="/evenements/alumni" className="dropdown-item">AlumniDay</NavLink>
                                <NavLink to="/evenements/bal" className="dropdown-item">Bal des polytechniciens</NavLink>
                                <NavLink to="/evenements/celebra" className="dropdown-item">Celebra Science</NavLink>
                                <NavLink to="/evenements/charity" className="dropdown-item">Charity Festival</NavLink>
                                <NavLink to="/evenements/don-de-sang" className="dropdown-item">Don de sang</NavLink>
                                <NavLink to="/evenements/interspe" className="dropdown-item">Interspé</NavLink>
                                <NavLink to="/evenements/mun" className="dropdown-item">Model united nations</NavLink>
                                <NavLink to="/evenements/sms" className="dropdown-item"> Stock Market Simulation</NavLink>
                                <NavLink to="/evenements/unleash" className="dropdown-item">Unleash</NavLink>
                                <NavLink to="/evenements/wikistage" className="dropdown-item">WikiStage</NavLink>
                            </div>
                    </li>
                    <li className="nav-item">
                        <NavLink to="/about" className="nav-link">Qui sommes-nous?</NavLink>
                    </li>
                </ul>
            </div>
            <div className="nav-item">
                <NavLink to="/" className="genric-btn primary circle">Polytechnique</NavLink>
            </div>

        </nav>
        </div>
            </div>
        </div>
        <br/>
    </header>




    );
}

export default NavbarSection;
